import React, { useState } from 'react';
import {
  Dialog, DialogTitle, DialogContent, DialogActions,
  Button, TextField, Typography, Box
} from '@mui/material';
import { StockItem } from './types';

interface AddUpdateModalProps {
  isOpen: boolean;
  item: StockItem; // Pass the item for context
  onSubmit: (note: string) => void;
  onClose: () => void;
}

const AddUpdateModal: React.FC<AddUpdateModalProps> = ({ isOpen, item, onSubmit, onClose }) => {
  const [note, setNote] = useState('');
  const [error, setError] = useState('');

  const handleSubmit = () => {
    if (!note.trim()) {
      setError('Please enter an update note.');
      return;
    }
    onSubmit(note);
    onClose(); // Close modal on successful submission
  };

  return (
    <Dialog open={isOpen} onClose={onClose} maxWidth="sm" fullWidth>
      <DialogTitle>
        <Typography variant="h6">Add Issue Update for:</Typography>
        <Typography variant="body2" color="textSecondary" sx={{ fontStyle: 'italic' }}>
          {item.productName} - {item.deliveryName}
        </Typography>
      </DialogTitle>
      <DialogContent dividers>
        {/* Display existing issue description for context */} 
        {item.issueDescription && (
          <Box sx={{ mb: 2, p: 1.5, background: '#f8f9fa', border: '1px solid #dee2e6', borderRadius: 1 }}>
            <Typography variant="body2"><strong>Initial Issue:</strong> {item.issueDescription}</Typography>
          </Box>
        )}

        {/* Update Note TextField */} 
        <TextField
          autoFocus
          margin="dense"
          id="updateNote"
          label="Update Note *"
          type="text"
          fullWidth
          multiline
          rows={4}
          variant="outlined"
          value={note}
          onChange={(e) => {
            setNote(e.target.value);
            if (error) setError(''); // Clear error on type
          }}
          error={!!error}
          helperText={error || 'Enter details about the update.'}
          placeholder="e.g., Contacted seller, awaiting response..."
        />
      </DialogContent>
      <DialogActions sx={{ padding: '16px 24px' }}>
        <Button onClick={onClose} color="secondary">
          Cancel
        </Button>
        <Button onClick={handleSubmit} variant="contained" color="primary">
          Save Update Note
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default AddUpdateModal; 