import React from 'react';
import {
  Menu,
  MenuItem,
  ListItemIcon,
  ListItemText,
} from '@mui/material';
import { ActionDefinition } from './types'; // Assuming types.ts is in the same directory

// Interface (Moved from ToolApp.tsx)
export interface ActionMenuProps {
  itemId: string;
  actions: ActionDefinition[];
  onActionSelected: (itemId: string, action: ActionDefinition) => void;
  onClose: () => void;
  anchorEl: null | HTMLElement;
  open: boolean;
}

// Component (Moved from ToolApp.tsx)
export const ActionMenu: React.FC<ActionMenuProps> = ({ 
  itemId, 
  actions, 
  onActionSelected, 
  onClose, 
  anchorEl, 
  open 
}) => {
  const handleActionClick = (action: ActionDefinition) => {
    onActionSelected(itemId, action);
    onClose(); // Close the menu after action selection
  };

  return (
    <Menu
      anchorEl={anchorEl}
      open={open}
      onClose={onClose}
      MenuListProps={{
        'aria-labelledby': `actions-button-${itemId}`, // Improve accessibility
      }}
    >
      {actions.map(action => (
        <MenuItem 
          key={action.id} 
          onClick={() => handleActionClick(action)}
        >
          {action.icon && (
            <ListItemIcon sx={{ minWidth: '32px' }}> 
              {action.icon}
            </ListItemIcon>
          )}
          <ListItemText primary={action.label} />
        </MenuItem>
      ))}
    </Menu>
  );
};

export default ActionMenu; 