import React, { useState, useEffect, Fragment } from 'react';
import { 
  getFirestore, collection, query, where, orderBy, limit, onSnapshot, Timestamp, 
  getDocs, QuerySnapshot, QueryDocumentSnapshot, FirestoreError, Firestore 
} from 'firebase/firestore';
import { 
  Dialog, DialogTitle, DialogContent, DialogActions, 
  Button, Typography, Box, CircularProgress, List, ListItem, ListItemText, Divider 
} from '@mui/material';
import { LogActionData, ActionDefinition } from './types'; 

interface UserActivityViewProps {
  isOpen: boolean;
  db: Firestore;
  orgId: string;
  onClose: () => void;
}

const getActionDescription = (log: LogActionData): string => {
  switch (log.action) {
    case 'item.create':
      return 'Item created';
    case 'item.update.fields': {
      if (log.before && log.after) {
        const fields = (log.details?.changedFields as string[] | undefined)
          || Object.keys(log.after).filter(k => JSON.stringify(log.before?.[k]) !== JSON.stringify(log.after?.[k]));
        if (fields.length) {
          const changes = fields.map(f => `${f}: ${log.before?.[f]}  ${log.after?.[f]}`);
          return `Edited ${changes.join(', ')}`;
        }
      }
      const simple = (log.details?.changedFields as string[] | undefined)?.join(', ');
      return simple ? `Edited fields: ${simple}` : 'Item edited';
    }
    default:
      if (log.action.startsWith('item.update.status.')) {
        const oldStat = log.details?.previousStatus || (log.before?.currentStatus as string) || 'Unknown';
        const newStat = log.details?.newStatus || log.action.replace('item.update.status.', '');
        return `Status changed: ${oldStat}  ${newStat}`;
      }
      if (log.action === 'item.update.flag') {
        const flagged = log.details?.isFlagged;
        return flagged ? 'Item flagged' : 'Item unflagged';
      }
      if (log.action === 'item.update.issue.report') {
        return `Issue reported: ${log.details?.description || log.details?.issueDescription || ''}`;
      }
      if (log.action === 'item.update.issue.update') {
        return `Issue update added: ${log.details?.note || ''}`;
      }
      if (log.action === 'item.update.issue.resolve') {
        return `Issue resolved: ${log.details?.resolutionOutcome || ''}`;
      }
      if (log.action === 'item.add_note') {
        return `Note added: ${log.details?.note || ''}`;
      }
      return log.action;
  }
};

const UserActivityView: React.FC<UserActivityViewProps> = ({ isOpen, db, orgId, onClose }) => {
  const [logs, setLogs] = useState<LogActionData[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);
  const [members, setMembers] = useState<Record<string, string>>({});

  useEffect(() => {
    if (!orgId) return;
    const membersRef = collection(db, `orgs/${orgId}/members`);
    getDocs(membersRef)
      .then((snapshot: QuerySnapshot) => {
        const map: Record<string, string> = {};
        snapshot.docs.forEach((doc: QueryDocumentSnapshot) => {
          const data: any = doc.data();
          map[doc.id] = data.email || doc.id;
        });
        setMembers(map);
      })
      .catch((err: FirestoreError) => console.error('Error fetching members:', err));
  }, [orgId, db, setMembers]);

  useEffect(() => {
    if (!orgId) {
      setError('Organization ID is required.');
      setLoading(false);
      return;
    }

    setLoading(true);
    setError(null);
    const logsRef = collection(db, `orgs/${orgId}/logs`);
    const q = query(
      logsRef,
      where('tool', '==', 'incomingtool'),
      orderBy('ts', 'desc'),
      limit(100)
    );

    const unsubscribe = onSnapshot(q, (snapshot: QuerySnapshot) => {
      const fetchedLogs = snapshot.docs.map(doc => {
        const d = doc.data() as any;
        return {
          orgId,
          toolKey: d.toolKey || d.tool,
          action: d.action,
          entityPath: d.entityPath,
          timestamp: d.timestamp || d.ts,
          userId: d.userId || d.uid,
          userEmail: d.email, 
          before: d.before,
          after: d.after,
          details: d.details,
        } as LogActionData;
      });
      const relevantLogs = fetchedLogs.filter(log => log.action !== 'app-loaded');
      setLogs(relevantLogs);
      setLoading(false);
    }, (err) => {
      console.error("Error fetching user activity logs: ", err); 
      const firestoreError = err as FirestoreError; 
      setError(`Failed to load activity logs: ${firestoreError.message}`);
      setLoading(false);
    });

    return () => unsubscribe();
  }, [orgId, db, setLogs]);

  const renderLogEntryContent = (log: LogActionData) => {
    const time = log.timestamp instanceof Timestamp
      ? log.timestamp.toDate().toLocaleString()
      : typeof log.timestamp === 'string'
      ? new Date(log.timestamp).toLocaleString()
      : 'Invalid Date';
    const user = log.userEmail || members[log.userId || ''] || log.userId || 'system';
    const actionDesc = getActionDescription(log);
    const parts = log.entityPath.split('/');
    const entityId = parts.length >= 2 ? parts[1] : '';
    const qty = log.before?.quantity;
    const name = log.before?.productName as string | undefined;
    const entityLabel = qty && name
      ? `${qty}x ${name}`
      : ((log.before?.deliveryName as string) || name || entityId);

    return (
      <ListItemText
        primary={
          <Fragment>
            <Typography sx={{ display: 'inline' }} component="span" variant="body2" color="text.primary">
              {time} - {actionDesc} by {user}
            </Typography>
            <Typography 
              component="span" 
              variant="body2" 
              sx={{ ml: 1, color: 'text.secondary' }}
            >
              {entityLabel}
            </Typography>
          </Fragment>
        }
      />
    );
  };

  return (
    <Dialog open={isOpen} onClose={onClose} maxWidth="md" fullWidth scroll="paper">
      <DialogTitle>
        <Typography variant="h6">User Activity Log</Typography>
        <Typography variant="caption">Last 100 entries</Typography>
      </DialogTitle>
      <DialogContent dividers>
        {loading && (
          <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', minHeight: '200px' }}> 
            <CircularProgress />
          </Box>
        )}
        {error && (
          <Typography color="error" sx={{ textAlign: 'center', mt: 2 }}>
            {error}
          </Typography>
        )}
        {!loading && !error && (
          <List dense sx={{ width: '100%', bgcolor: 'background.paper', p: 0 }}> 
            {logs.length === 0 ? (
              <ListItem>
                <ListItemText primary="No activity logs found." />
              </ListItem>
            ) : (
              logs.map((log, index) => (
                <Fragment key={`${log.timestamp}-${index}-${log.action}`}> 
                  <ListItem alignItems="flex-start" sx={{ py: 1.5 }}> 
                    {renderLogEntryContent(log)}
                  </ListItem>
                  {index < logs.length - 1 && <Divider variant="inset" component="li" />} 
                </Fragment>
              ))
            )}
          </List>
        )}
      </DialogContent>
      <DialogActions sx={{ padding: '16px 24px' }}>
        <Button onClick={onClose} color="primary">
          Close
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default UserActivityView;
