import React from 'react';
import {
  Box,
  Typography,
  Button,
  IconButton,
  Tooltip,
} from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import SettingsIcon from '@mui/icons-material/Settings'; // Renamed for clarity
import HistoryIcon from '@mui/icons-material/History';

interface TopBarProps {
  onAddItem: () => void;
  onOpenSettings: () => void;
  onOpenActivity: () => void;
}

const TopBar: React.FC<TopBarProps> = ({
  onAddItem,
  onOpenSettings,
  onOpenActivity,
}) => {
  return (
    <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
      <Typography variant="h6">Stock Items</Typography>
      <Box>
        <Tooltip title="Add New Item">
          <Button
            variant="contained"
            startIcon={<AddIcon />}
            onClick={onAddItem}
            sx={{ mr: 1 }}
          >
            Add Item
          </Button>
        </Tooltip>
        <Tooltip title="Table Settings">
          <IconButton onClick={onOpenSettings} sx={{ mr: 1 }}>
            <SettingsIcon />
          </IconButton>
        </Tooltip>
        <Tooltip title="View User Activity">
          <IconButton onClick={onOpenActivity}>
            <HistoryIcon />
          </IconButton>
        </Tooltip>
      </Box>
    </Box>
  );
};

export default TopBar; 