import React from 'react';
import {
  Box,
  TextField,
  Typography,
  Checkbox,
  FormControlLabel,
  Tabs,
  Tab,
} from '@mui/material';
import { ItemStatus } from './types'; // Assuming types.ts is in the same directory

interface FilterControlsProps {
  searchTerm: string;
  onSearchTermChange: (term: string) => void;
  showFlaggedOnly: boolean;
  onShowFlaggedOnlyChange: (checked: boolean) => void;
  currentFilter: ItemStatus | 'All';
  onCurrentFilterChange: (filter: ItemStatus | 'All') => void;
}

const FilterControls: React.FC<FilterControlsProps> = ({
  searchTerm,
  onSearchTermChange,
  showFlaggedOnly,
  onShowFlaggedOnlyChange,
  currentFilter,
  onCurrentFilterChange,
}) => {
  return (
    // Wrap both sections in a Fragment or Box
    <React.Fragment>
      {/* Search and Flag Toggle Controls */}
      <Box sx={{ display: 'flex', alignItems: 'center', gap: 2, mb: 2 }}>
        <TextField
          label="Search Items"
          variant="outlined"
          size="small"
          fullWidth
          sx={{ flexGrow: 1 }}
          value={searchTerm}
          onChange={(e) => onSearchTermChange(e.target.value)}
        />
        <FormControlLabel 
          control={
            <Checkbox 
              checked={showFlaggedOnly}
              onChange={(e) => onShowFlaggedOnlyChange(e.target.checked)}
              size="small" 
            />
          }
          label={<Typography variant="body2">Show Flagged Only</Typography>}
          sx={{ mr: 0, whiteSpace: 'nowrap' }} // Prevent wrap
        />
      </Box>

      {/* Filter Tabs */}
      <Box sx={{ borderBottom: 1, borderColor: 'divider', mb: 2 }}>
        <Tabs 
          value={currentFilter}
          // Type assertion needed for MUI Tabs onChange event
          onChange={(event, newValue: ItemStatus | 'All') => onCurrentFilterChange(newValue)} 
          variant="scrollable"
          scrollButtons="auto"
          allowScrollButtonsMobile 
          aria-label="Stock item status filter"
        >
          <Tab label="All" value="All" />
          <Tab label="Pending" value="Pending Delivery" />
          <Tab label="Delivered" value="Delivered" />
          <Tab label="Issue" value="Issue" />
          <Tab label="Late" value="Late" />
          <Tab label="Archived" value="Archived" />
        </Tabs>
      </Box>
    </React.Fragment>
  );
};

export default FilterControls; 