import React, { useState } from 'react';
import {
  Dialog, DialogTitle, DialogContent, DialogActions,
  Button, TextField, Typography
} from '@mui/material';
import { StockItem } from './types'; // Import StockItem if needed for context

interface ReportIssueModalProps {
  isOpen: boolean;
  item: StockItem; // Pass the item for context
  onSubmit: (description: string) => void;
  onClose: () => void;
}

const ReportIssueModal: React.FC<ReportIssueModalProps> = ({ isOpen, item, onSubmit, onClose }) => {
  const [description, setDescription] = useState('');
  const [error, setError] = useState('');

  const handleSubmit = () => {
    if (!description.trim()) {
      setError('Please enter a description of the issue.');
      return;
    }
    onSubmit(description);
    onClose(); // Close modal after successful submission
  };

  return (
    <Dialog open={isOpen} onClose={onClose} maxWidth="sm" fullWidth>
      <DialogTitle>
        <Typography variant="h6">Report Issue for:</Typography>
        <Typography variant="body2" color="textSecondary" sx={{ fontStyle: 'italic' }}>
          {item.productName} - {item.deliveryName}
        </Typography>
      </DialogTitle>
      <DialogContent dividers>
        <TextField
          autoFocus
          margin="dense"
          id="issueDescription"
          label="Describe the Issue *"
          type="text"
          fullWidth
          multiline
          rows={4}
          variant="outlined"
          value={description}
          onChange={(e) => {
            setDescription(e.target.value);
            if (error) setError(''); // Clear error on type
          }}
          error={!!error}
          helperText={error || 'Please provide details about the problem.'}
        />
      </DialogContent>
      <DialogActions sx={{ padding: '16px 24px' }}>
        <Button onClick={onClose} color="secondary">
          Cancel
        </Button>
        <Button onClick={handleSubmit} variant="contained" color="primary">
          Save Issue Report
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default ReportIssueModal;