import React, { useState } from 'react';
import {
  Dialog, DialogTitle, DialogContent, DialogActions,
  Button, TextField, Select, MenuItem, FormControl, InputLabel, Typography,
  FormHelperText, Box
} from '@mui/material';
import { StockItem } from './types';

interface ResolveIssueModalProps {
  isOpen: boolean;
  item: StockItem; // Pass the item for context
  onSubmit: (outcome: string, note?: string) => void;
  onClose: () => void;
}

const RESOLUTION_OUTCOMES = [
  "Item Accepted / Kept As Is",
  "Item Repaired / Refurbished",
  "Partial Refund Received",
  "Returned to Supplier",
  "Disposed Of",
  "Other" // Allows adding notes for specifics
];

const ResolveIssueModal: React.FC<ResolveIssueModalProps> = ({ isOpen, item, onSubmit, onClose }) => {
  const [outcome, setOutcome] = useState('');
  const [note, setNote] = useState('');
  const [error, setError] = useState('');

  const handleSubmit = () => {
    setError(''); // Clear previous error
    if (!outcome) {
      setError('Please select a resolution outcome.');
      return;
    }
    onSubmit(outcome, note.trim() || undefined); // Pass note only if it has content
  };

  return (
    <Dialog open={isOpen} onClose={onClose} maxWidth="sm" fullWidth>
      <DialogTitle>
        <Typography variant="h6">Resolve Issue for:</Typography>
        <Typography variant="body2" color="textSecondary" sx={{ fontStyle: 'italic' }}>
          {item.productName} - {item.deliveryName}
        </Typography>
      </DialogTitle>
      <DialogContent dividers>
        {/* Display existing issue description */}
        {item.issueDescription && (
          <Box sx={{ mb: 2, p: 1.5, background: '#f8f9fa', border: '1px solid #dee2e6', borderRadius: 1 }}>
            <Typography variant="body2"><strong>Initial Issue:</strong> {item.issueDescription}</Typography>
          </Box>
        )}

        {/* Resolution Outcome Select */}
        <FormControl fullWidth error={!!error} sx={{ mb: 2 }}>
          <InputLabel id="resolution-outcome-label">Resolution Outcome *</InputLabel>
          <Select
            labelId="resolution-outcome-label"
            id="resolutionOutcome"
            value={outcome}
            label="Resolution Outcome *"
            onChange={(e) => {
              setOutcome(e.target.value as string);
              if (error) setError(''); // Clear error on change
            }}
          >
            <MenuItem value="" disabled><em>-- Select an Outcome --</em></MenuItem>
            {RESOLUTION_OUTCOMES.map(opt => (
              <MenuItem key={opt} value={opt}>{opt}</MenuItem>
            ))}
          </Select>
          {error && <FormHelperText>{error}</FormHelperText>}
        </FormControl>

        {/* Resolution Notes TextField */}
        <TextField
          id="resolutionNotes"
          label="Resolution Notes (Optional)"
          multiline
          rows={3}
          fullWidth
          value={note}
          onChange={(e) => setNote(e.target.value)}
          placeholder="e.g., Supplier issued refund, repaired screen..."
          variant="outlined"
          sx={{ mb: 2 }}
        />
      </DialogContent>
      <DialogActions sx={{ padding: '16px 24px' }}>
        <Button onClick={onClose} color="secondary">
          Cancel
        </Button>
        <Button onClick={handleSubmit} variant="contained" color="primary">
          Save Resolution
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default ResolveIssueModal;