import { useState, useEffect, useMemo } from 'react';
import { collection, query, where, orderBy, onSnapshot, Query } from 'firebase/firestore';
import { db } from 'shell/firebase'; // Assuming db is correctly imported via MF
import { StockItem, ItemStatus } from '../types';

export const useStockItems = (orgId: string | undefined) => {
  // State for non-archived items
  const [nonArchivedItems, setNonArchivedItems] = useState<StockItem[]>([]);
  const [mainLoading, setMainLoading] = useState<boolean>(true);
  const [mainError, setMainError] = useState<string | null>(null);

  // State for archived items
  const [archivedItems, setArchivedItems] = useState<StockItem[]>([]);
  const [archivedLoading, setArchivedLoading] = useState<boolean>(true);
  const [archivedError, setArchivedError] = useState<string | null>(null);

  // Filter state remains the same
  const [searchTerm, setSearchTerm] = useState<string>('');
  const [currentFilter, setCurrentFilter] = useState<ItemStatus | 'All'>('All');
  const [showFlaggedOnly, setShowFlaggedOnly] = useState<boolean>(false);

  // Combined loading and error state for the hook consumer
  const loading = mainLoading || archivedLoading;
  const error = mainError || archivedError;

  // --- Firestore Real-time Sync for Non-Archived Items ---
  useEffect(() => {
    console.log('[useStockItems] Non-Archived Listener: Checking db and orgId:', { db_instance: db, org_id: orgId });
    if (!db || !orgId) {
      console.warn('[useStockItems] Non-Archived Listener: db or orgId is missing, skipping Firestore query.');
      setMainLoading(false);
      if (!orgId) setNonArchivedItems([]);
      return;
    }

    setMainLoading(true);
    setMainError(null);
    console.log(`[useStockItems] Non-Archived Listener: Setting up listener for orgs/${orgId}/stockItems (non-archived)`);
    
    // Query for non-archived items
    const q = query(
        collection(db, 'orgs', orgId, 'stockItems') as Query<StockItem>, // Cast for type safety with Firestore V9
        where("currentStatus", "!=", "Archived"), 
        orderBy("currentStatus"), // Order by status first to group non-archived logically?
        orderBy('orderDate', 'desc') // Then by order date
    );
    
    const unsubscribe = onSnapshot(q, (snapshot) => {
      console.log('[useStockItems] Non-Archived Listener: Received snapshot.');
      const items: StockItem[] = snapshot.docs.map(docSnap => ({ ...docSnap.data(), id: docSnap.id }));
      setNonArchivedItems(items);
      setMainLoading(false);
    }, (err) => {
      console.error('[useStockItems] Non-Archived Listener: Snapshot error:', err);
      setMainError('Failed to load items: ' + (err as Error).message);
      setMainLoading(false);
    });

    return () => {
      console.log('[useStockItems] Non-Archived Listener: Cleaning up listener.');
      unsubscribe();
    };
  }, [orgId]);

  // --- Firestore Real-time Sync for Archived Items ---
  useEffect(() => {
    console.log('[useStockItems] Archived Listener: Checking db and orgId:', { db_instance: db, org_id: orgId });
    if (!db || !orgId) {
      console.warn('[useStockItems] Archived Listener: db or orgId is missing, skipping Firestore query.');
      setArchivedLoading(false);
      if (!orgId) setArchivedItems([]);
      return;
    }

    setArchivedLoading(true);
    setArchivedError(null);
    console.log(`[useStockItems] Archived Listener: Setting up listener for orgs/${orgId}/stockItems (archived)`);
    
    // Query for archived items
    const qArchived = query(
        collection(db, 'orgs', orgId, 'stockItems') as Query<StockItem>,
        where("currentStatus", "==", "Archived"), 
        orderBy('orderDate', 'desc')
    );
    
    const unsubscribeArchived = onSnapshot(qArchived, (snapshot) => {
      console.log('[useStockItems] Archived Listener: Received snapshot.');
      const items: StockItem[] = snapshot.docs.map(docSnap => ({ ...docSnap.data(), id: docSnap.id }));
      setArchivedItems(items);
      setArchivedLoading(false);
    }, (err) => {
      console.error('[useStockItems] Archived Listener: Snapshot error:', err);
      setArchivedError('Failed to load archived items: ' + (err as Error).message);
      setArchivedLoading(false);
    });

    return () => {
      console.log('[useStockItems] Archived Listener: Cleaning up listener.');
      unsubscribeArchived();
    };
  }, [orgId]);

  // --- Filtering Logic (Updated) ---
  const filteredItems = useMemo(() => {
    // Determine the source list based on the current filter
    const sourceItems = currentFilter === 'Archived' ? archivedItems : nonArchivedItems;

    return sourceItems.filter(item => {
      const lowerSearchTerm = searchTerm.toLowerCase();
      const matchesSearch = (
        (item.deliveryName?.toLowerCase().includes(lowerSearchTerm)) ||
        (item.productName?.toLowerCase().includes(lowerSearchTerm)) ||
        (item.seller?.toLowerCase().includes(lowerSearchTerm)) ||
        (item.asinSku?.toLowerCase().includes(lowerSearchTerm))
      );
        
      // Filter logic based on the already selected source list
      let matchesFilter = false;
      if (currentFilter === 'All' || currentFilter === 'Archived') {
          // If 'All', we use nonArchivedItems (already filtered by query)
          // If 'Archived', we use archivedItems (already filtered by query)
          matchesFilter = true;
      } else if (currentFilter === 'Late') {
          // Note: isItemLate logic is now primarily for highlighting in StockItemList
          // We still need to filter correctly when the 'Late' tab is selected.
          // We need access to the original isItemLate logic here for filtering.
          // Let's redefine it locally for the filter memo, or ideally import from utils.
          const checkItemLate = (itemToCheck: StockItem): boolean => {
            if (itemToCheck.currentStatus !== 'Pending Delivery') return false;
            try {
              const orderDate = new Date(itemToCheck.orderDate);
              const sevenDaysAgo = new Date();
              sevenDaysAgo.setDate(sevenDaysAgo.getDate() - 7);
              return orderDate < sevenDaysAgo;
            } catch (e) {
                console.error("Error parsing orderDate for late check:", e);
                return false; // Treat parse errors as not late
            }
          };
          matchesFilter = checkItemLate(item);
      } else {
          matchesFilter = item.currentStatus === currentFilter;
      }

      const matchesFlag = !showFlaggedOnly || item.isFlagged === true;

      return matchesFilter && matchesSearch && matchesFlag;
    });
  }, [nonArchivedItems, archivedItems, searchTerm, currentFilter, showFlaggedOnly]); // Include both lists in dependencies

  return {
    // Provide access to both lists if needed externally, though ToolApp mainly uses `items`
    allItems: nonArchivedItems, 
    archivedItems: archivedItems, 
    items: filteredItems, // The items to display based on currentFilter
    loading,
    error,
    searchTerm,
    currentFilter,
    showFlaggedOnly,
    setSearchTerm,
    setCurrentFilter,
    setShowFlaggedOnly,
    setError: (err: string | null) => { // Allow clearing both errors
        setMainError(err);
        setArchivedError(err);
    }
  };
}; 