import { StockItem, ItemStatus, ActivityEvent, ActivityEventType } from './types';

// Utility to remove undefined fields before saving to Firestore
export function removeUndefinedFields(obj: Record<string, any>) {
  return Object.fromEntries(Object.entries(obj).filter(([_, v]) => v !== undefined));
}

// Function to check if an item is considered late
export const isItemLate = (item: StockItem): boolean => {
  if (item.currentStatus !== 'Pending Delivery') {
    return false;
  }
  // Ensure orderDate is valid before creating a Date object
  if (!item.orderDate || isNaN(new Date(item.orderDate).getTime())) {
      console.warn("Invalid or missing orderDate for late check:", item.id);
      return false; 
  }
  const orderDate = new Date(item.orderDate);
  const sevenDaysAgo = new Date();
  sevenDaysAgo.setDate(sevenDaysAgo.getDate() - 7);
  return orderDate < sevenDaysAgo;
};

// Function to create a new ActivityEvent
export const createActivityEvent = (
  userId: string, 
  type: ActivityEventType,
  details: ActivityEvent['details'] = {}
): ActivityEvent => ({
  timestamp: new Date().toISOString(),
  type,
  userId,
  details,
}); 